import { useState, useEffect } from "react";
import { Link, useRouteMatch } from "react-router-dom";

import PageHeading from '../components/PageHeading/PageHeading';
import * as api from '../services/api';

export default function HomeView() {

    const { url } = useRouteMatch();
    const [films, setFilms] = useState([]);
    const [page, setPage] = useState(1);


    useEffect(() => {
        api.fetchMoovies(page).then(setFilms)
    }, [page])


    return (
        <>
            <PageHeading text="Popular movies" />

            {films &&
                <ul>
                    {films.map(film => {
                        return (
                            <li className='item' key={film.id}>
                                <Link to={`${url}${film.id}`}>
                                    <div className='video-card'>
                                        {film.poster_path ?
                                            <img
                                                className='gallery__video'
                                                src={`https://image.tmdb.org/t/p/w300${film.poster_path}`}
                                                // src="https://image.tmdb.org/t/p/w300/4O40vu2KOgnFfxvrZgzBG6havQ.jpg"
                                                alt={film.original_title}
                                            />
                                            :
                                            <img
                                                className='gallery__video'
                                                src='https://bookslibs.info/assets/general/images/no_poster.jpg'
                                                alt={film.original_title}
                                            />
                                        }
                                    </div>
                                    <div className='video__text'>
                                        <h2 className='video-title' id='video-title-dt'>{film.original_title}</h2>
                                        <span className='video__details'>release: {film.release_date}</span>
                                        <span className='video__vote'>raiting: {film.vote_average}</span>
                                    </div>

                                </Link>
                            </li>
                        )
                    })}
                </ul>}
        </>
    );
}



